.buttonContainer{
  text-align: center;
  position: relative;
  min-width: 100%;
}

.buttonContainer > * {
  display: inline-block;
  margin: 10px;
}

.Cabinet{
  position: absolute;
  display: inline-block;
  height: 100vh;
  overflow: scroll;
  width:30%;
  background-color: #f9f9f9;

}

.Clock{
  display: inline-block;
  width: 70%;
  height: 100vh;
}

@media only screen and (max-width: 600px){
  .Clock,  .Cabinet{
    width: 100%;
    display: block;
  }
}

.bttn{
  color: Grey;
  padding: 8px;
